@import 'defaults.scss';
@import 'brand.scss';

body {
  background-color: $color2;
  font-family: "Raleway", sans-serif;
  height : 100%;
  margin: 0;
  display: grid;
  grid-template-columns: [left-ws] 20% [content-col] 60% [right-ws] 20% [right-end];
  grid-template-rows: [header-img] 90% [navbar] 10% [content-row] auto [footer] 10% [page-end];
  grid-template-areas: 
    "bg bg bg"
    ". nav ."
    ". content ."
    ". footer-content .";
}

.bg {
  grid-column: left-ws / right-end;
  grid-row: header-img / content-row;
  background-color: red;
  z-index: -1;
}

nav {
  grid-area: nav;
  background-color: green;
}

main {
  grid-area: content;
  background-color: blue;

}

